import {
  React,
  Swal,
  connect,
  Link,
  reduxForm,
  Panel,
  PanelHeader,
  Tabel,
  ModalGlobal,
  showModal,
  hideModal,
  localStorageencryp,
  localStoragedecryp,
  setItem,
  reset,
  getItem,
  ToastNotification,
  PanelBody,
} from "../../components/helpers/library";
import { postData2 } from "../../config/axios";
import ModalDataCustomer from "./ModalDataCustomer";
import ModalPembayaran from "./ModalPembayaran";
import ModalTambahBarangPesanan from "./ModalTambahBarangPesanan";
import Loading from 'react-fullscreen-loading';

class Pesanan extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      loadingreset: false,
      FormModal: "",
      isLoadingPembayranTitipan: false,
      isLoadingPembayran: false,
      columns: [
        {
          dataField: "kode_kategori",
          text: "Kategori",
          footerAttrs: {
            colSpan: "5",
          },
          footer: "Total",
        },
        {
          dataField: "kode_barcode",
          text: "Kode Barcode",
        },
        {
          dataField: "keterangan",
          text: "Nama Barang",
        },
        {
          dataField: "perkiraan_berat",
          text: "Perkiraan Berat",
        },
        {
          dataField: "perkiraan_kadar",
          text: "Perkiraan Kadar",
        },
        {
          dataField: "harga_gram",
          text: "Harga / Gram",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "sub_total",
          text: "Sub Total",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },

        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row, index) => {
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    type="button"
                    onClick={() => this.hapusdata(index, row)}
                    className="btn btn-danger mr-3"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
          footer: "",
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(data) {
    let hasil = {
      alamat_customer: data.alamat_customer || "-",
      jenis_pelanggan: data.jenis_pelanggan || "NONMEMBER",
      kode_customer: data.jenis_pelanggan === "NONMEMBER" ? "NONMEMBER" : data.kode_customer,
      kode_sales: data.kode_sales,
      nama_customer: data.nama_customer || "REGULER",
      no_hp: data.no_hp || "-"
    }
    localStorageencryp("data_customer_tmp", JSON.stringify(hasil));
    this.setState({
      isLoadingDataCustomer: true,
    });
    setTimeout(() => {
      this.setState({
        isLoadingDataCustomer: false,
      });
      this.props.dispatch(hideModal());
    }, 500);
  }
  ShowModalCustomer() {
    this.setState({ FormModal: "ShowModalCustomer" });
    this.props.dispatch(showModal());
  }
  ShowModalBarang() {
    this.setState({ FormModal: "ShowModalBarang" });
    this.props.dispatch(showModal());
  }
  simpanBarang(data) {
    // let cek =
    //   getItem("data_barang_pesanan").length === 0
    //     ? []
    //     : getItem("data_barang_pesanan");
    // let hasil_cek = cek.findIndex((hasil) => hasil.kode_barcode === "");
    // if (hasil_cek === -1) {
    let cek =[]
      let row = {
        kode_kategori: data.kode_group,
        perkiraan_berat: parseFloat(data.berat),
        perkiraan_kadar: parseInt(data.kadar),
        harga_gram: parseInt(data.harga_pergram),
        attribut: data.atribut || "-",
        harga_atribut: parseInt(data.harga_atribut || 0),
        ongkos: parseInt(data.ongkos || 0),
        sub_total: parseInt(data.sub_total),
        keterangan: data.jenis_barang || "-",
        deskripsi: data.deskripsi_pesanan || "-",
        barang_contoh_internal:
          data.options_value === "dari_toko" ? true : false,
        kode_barcode: data.kode_dari_toko || "-",
        deskripsi_contoh:
          data.options_value === "dari_toko"
            ? data.deskripsi_dari_toko || "-"
            : data.deskripsi_bawa_sediri || "-",
        berat_barang_contoh:
          data.options_value === "dari_toko"
            ? data.berat_dari_toko || 0
            : data.berat_bawa_sendiri || 0,
      };
      cek.push(row);
      setItem("data_barang_pesanan", cek);
      this.setState({
        status: true,
      });
    // }

    this.props.dispatch(reset("ModalTambahBarangPesanan"));
    this.props.dispatch(hideModal());
  }
  hapusdata(id, row) {
    Swal.fire({
      title: "Anda Yakin !!",
      text: "Ingin Menghapus Data Ini ?",
      icon: "warning",
      position: "top-center",
      cancelButtonText: "Tidak",
      showCancelButton: true,
      confirmButtonText: "OK",
      showConfirmButton: true,
    }).then((result) => {
      if (result.isConfirmed) {
        let data = getItem("data_barang_pesanan");
        data.splice(id, 1);
        setItem("data_barang_pesanan", data);
        this.setState({
          status: "berhasil",
        });
      }
    });
  }
  bayarSekarang() {
    this.setState({
      isLoadingPembayranTitipan: true,
    });
    let data_estimasi = getItem("estimasi_pesanan");
    let pembayaran = getItem("trx_pemabayaran_pesanan");

    let data_pembayaran = [];
    let total_pembayarn = 0;
    let jumlah_rp = 0;
    pembayaran.forEach((element) => {
      if (element.jenis === "CASH") {
        if (parseInt(element.nominal) <= Math.abs(parseInt(getItem("sisa_bayar_pesanan").sisa_bayar))) {
          jumlah_rp = Math.abs(parseInt(getItem("sisa_bayar_pesanan").sisa_bayar) - parseInt(element.nominal));
        } else {
          jumlah_rp = parseInt(element.nominal);
        }
        if (parseInt(element.nominal) >= Math.abs(parseInt(getItem("sisa_bayar_pesanan").total_transaksi))) {
          jumlah_rp = parseInt(element.nominal) - Math.abs(parseInt(getItem("sisa_bayar_pesanan").sisa_bayar));
        } else {
          jumlah_rp = parseInt(element.nominal);
        }
      } else {
        jumlah_rp = parseInt(element.nominal);
      }
      // console.log("JUMLAH RP - NOMINAL", element.nominal );
      // console.log("JUMLAH RP - SISA BAYAR", getItem('sisa_bayar_pesanan').sisa_bayar );
      let row = {
        jenis: element.jenis,
        keterangan: element.keterangan,
        fee: element.jenis === "CREDIT" ? parseInt(element.fee) || 0 : 0,
        jumlah_rp : jumlah_rp
        // jumlah_rp: element.nominal >= parseInt(getItem('sisa_bayar_pesanan').total_transaksi) ? parseInt(element.nominal) - parseInt(getItem('sisa_bayar_pesanan').sisa_bayar) : parseInt(element.nominal),
      };
      total_pembayarn = element.nominal + total_pembayarn;
      data_pembayaran.push(row);
    });

    let data_customer = JSON.parse(localStoragedecryp("data_customer_tmp"));
    let data_barang_penjualan = getItem("data_barang_pesanan");

    let detail_barang = [];
    let total_harga = 0;
    data_barang_penjualan.forEach((item, index) => {
      total_harga = item.sub_total + total_harga;
      let row = {
        kode_kategori: item.kode_kategori,
        perkiraan_berat: parseFloat(item.perkiraan_berat),
        perkiraan_kadar: parseInt(item.perkiraan_kadar),
        harga_gram: parseInt(item.harga_gram),
        attribut: item.attribut || "-",
        kode_barcode: item.kode_barcode|| "-",
        harga_atribut: parseInt(item.harga_atribut),
        ongkos: parseInt(item.ongkos || 0),
        sub_total: parseInt(item.sub_total),
        keterangan: item.keterangan || "-",
        deskripsi: item.deskripsi || "-",
        barang_contoh_internal: item.barang_contoh_internal || false,
        deskripsi_contoh: item.deskripsi_contoh || "-",
        berat_barang_contoh: parseFloat(item.berat_barang_contoh),
      };
      detail_barang.push(row);
    });
    let datapenjualan = {
      status: "PESANAN",
      estimasi_tanggal: data_estimasi,
      kode_sales: data_customer.kode_sales,
      kode_customer:
        data_customer.jenis_pelanggan === "NONMEMBER"
          ? data_customer.jenis_pelanggan
          : data_customer.kode_customer,
      nama_customer: data_customer.nama_customer,
      alamat_customer: data_customer.alamat_customer,
      no_hp: data_customer.no_hp,
      detail_barang: detail_barang,
      pembayaran: data_pembayaran,
      total_harga: total_harga,
      jumlah_bayar: total_pembayarn,
      sisa_bayar: Math.abs(parseInt(total_harga) - parseInt(total_pembayarn)),
    };
    postData2("pesanan/create", datapenjualan)
      .then((res) => {
        setItem("data_barang_pesanan", []);
        ToastNotification("success", "Transaksi Pesanan Berhasil");
if (res.data.length !== 0) {
let notapesanan = [];
for (let index = 0; index < 2; index++) {  
let feedback = '';
feedback += `========================================\n`
feedback += `               NOTA PESANAN             \n`
feedback += `========================================
Tanggal          : ${res.data.tanggal}
Nama Customer    : ${res.data.nama_customer}
No Hp            : ${res.data.no_hp}\n`
feedback += `========================================\n`
feedback += `Keterangan\n`
feedback += `========================================\n`
res.data.detail_barang.forEach((detail, index) => {
feedback += `No Faktur Pesanan  : ${res.data.no_pesanan}\n`
// feedback += `Nama Barang        : ${detail.nama_barang}\n`
feedback += `Dp                 : ${res.data.dp.toLocaleString("kr-KO")}\n`
feedback += `Estimasi Pesanan   : ${res.data.estimasi_tanggal}\n`
feedback += `========================================\n`;
feedback += `                               TTD      \n`
feedback += `                                        \n`
feedback += `                                        \n`
feedback += `                                        \n`
feedback += `                         (             )\n`
feedback += `                                        \n`
feedback += `                                        \n`
feedback += `                                        \n`
})
notapesanan.push(feedback)
}
document.getElementById("nota_pesanan").value = notapesanan.join('');;
setTimeout(() => {
this.TextFilenotapesanan();
}, 100);
}
      })
      .then(() => {
        this.props.dispatch(hideModal());
      })
      .then(() => {
        this.props.dispatch(reset("ModalPembayaran"));
      })
      .then(() => {
        setItem("data_barang_pesanan", []);
        setItem("trx_pemabayaran_pesanan", []);
        setItem("data_customer_tmp", []);
        setItem("estimasi_pesanan", []);
      })
      .then(() => {
        this.setState({
          isLoadingPembayranTitipan: false,
        });
      })
      .catch((err) => {
        this.setState({
          isLoadingPembayranTitipan: false,
        });
        ToastNotification("info", err.response === undefined ? "Terjadi Kesalahan Saat Request Data, Silahkan Ulangi Lagi" : err.response.data.message || "Terjadi Kesalahan Saat Request Data, Silahkan Ulangi Lagi");
      });
  }

  showModalPembayaran() {
    // document.getElementById('jenis_pembayaran').focus();
    let customer = getItem("data_customer_tmp") || [];
    let barang = getItem("data_barang_pesanan") || [];
    if (customer.length === 0 || barang.length === 0) {
      ToastNotification(
        "info",
        "Data Customer Atau Data Barang Belum Diisi !!!"
      );
    } else {
      this.setState({ FormModal: "ShowModalPembayaran" });
      this.props.dispatch(showModal());
    }
  }
  TextFilenotapesanan() {
    const element = document.createElement("a");
    const file = new Blob([document.getElementById("nota_pesanan").value], {
      type: "text/plain;charset=utf-8",
    });
    element.href = URL.createObjectURL(file);
    element.download = "autoprint_pesanan.txt";
    document.body.appendChild(element);
    element.click();
  }
  reset() {
    this.setState({
      loadingreset : true,
    })
    setTimeout(() => {
      setItem("data_barang_pesanan", []);
      setItem("trx_pemabayaran_pesanan", []);
      setItem("data_customer_tmp", []);
      setItem("estimasi_pesanan", []);
      this.setState({
        loadingreset : false,
      })
    }, 100);
  }
  render() {
    const expandRow = {
      renderer: (row) => (
        <div className="container">
          <div className="row">
            <div className="col-12">
              Ongkos : {row.ongkos.toLocaleString("kr-KO")} <br />
              Nama Atribut : {row.attribut} <br />
              Harga Atribut : {row.harga_atribut.toLocaleString("kr-KO")} <br />
              Betar Barang Contoh : {row.berat_barang_contoh} <br />
              Deskripsi Contoh : {row.deskripsi_contoh} <br />
              Deskripsi Pesanan : {row.deskripsi} <br />
            </div>
          </div>
        </div>
      ),
      showExpandColumn: true,
      expandByColumnOnly: true,
    };
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Pesanan</Link>
          </li>
          <li className="breadcrumb-item active">Transaksi Pesanan</li>
        </ol>
        <h1 className="page-header">Transaksi Pesanan </h1>
        <textarea
            style={{ display: "none" }}
            id="nota_pesanan"
            rows="100"
            cols="100"
          />
        <Panel>
          <PanelHeader>Transaksi Pesanan</PanelHeader>
          <br />
          {/* Transaksi Pesanan */}
          <PanelBody>
            <Tabel
              keyField="kode_barcode"
              exportCsv={false}
              data={getItem("data_barang_pesanan") || []}
              tambahData={true}
              btnText="Data Customer"
              columns={this.state.columns}
              btnOptional={true}
              namaCustomer={true}
              jenisCustomer={true}
              form="Penjualan"
              btnTextOptional="Data Barang"
              handleClick={() => this.ShowModalCustomer()}
              handleClickOptional={() => this.ShowModalBarang()}
              empty={true}
              textEmpty="Data Barang Kosong"
              expandRow={expandRow}
              margin
              // form="Service"
            />
            <div className="row">
              <div className="col-6 text-left">
                <button
                  className="btn btn-primary"
                  onClick={() => this.showModalPembayaran()}
                >
                  {" "}
                  Simpan Pesanan{" "}
                </button>
                &nbsp;
                <button
                  className="btn btn-warning"
                  onClick={() => this.reset()}
                >
                 {this.state.loadingreset ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Reset
                  </>
                ) : (
                  "Reset"
                )}
                </button>
              </div>
            </div>
            </PanelBody>
          <br />
          {/* End Tambah Blank Page  */}
        </Panel>
        <ModalGlobal
          size={
            this.state.FormModal === "ShowModalCustomer"
              ? "xl"
              : this.state.FormModal === "ShowModalBarang"
              ? "xl"
              : "l"
          }
          title={
            this.state.FormModal === "ShowModalCustomer"
              ? "Data Customer"
              : this.state.FormModal === "ShowModalBarang"
              ? "Tambah Barang Pesanan"
              : "Form Pembayaran"
          }
          content={
            this.state.FormModal === "ShowModalCustomer" ? (
              <ModalDataCustomer
                isLoadingDataCustomer={this.state.isLoadingDataCustomer}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            ) : this.state.FormModal === "ShowModalBarang" ? (
              <ModalTambahBarangPesanan
                isLoadingTopBar={this.props.isLoadingTopBar}
                hideModal={() => this.hideModa()}
                onSubmit={(data) => this.simpanBarang(data)}
              />
            ) : (
              <ModalPembayaran
                isLoadingPembayranTitipan={this.state.isLoadingPembayranTitipan}
                isLoadingPembayran={this.state.isLoadingPembayran}
                bayarSekarang={(data) => this.bayarSekarang(data)}
                simpanPembayaran={(data) => this.simpanPembayaran(data)}
              />
            )
          }
        />
        {this.props.isLoadingPembayranTitipan ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </div>
    );
  }
}

Pesanan = reduxForm({
  form: "Pesanan",
  enableReinitialize: true,
})(Pesanan);
export default connect()(Pesanan);
