import { GetDataGroup, hideModal } from "../../actions/datamaster_action";
import { pembulatan } from "../../components/helpers/function";
import {
  React,
  connect,
  reduxForm,
  ReanderSelect,
  Field,
  ReanderField,
  Gramasi,
  NumberOnly,
  createNumberMask,
  formValueSelector,
  ToastNotification,
  getData,
} from "../../components/helpers/library";
import { getData2 } from "../../config/axios";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  locale: "kr-KO",
});
class ModalTambahBarangPesanan extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      bawa_sendiri: false,
      dari_toko: true,
      isChecked: true,
      kategori_harga : true,
    };
  }

  componentDidMount() {
    this.props.dispatch(GetDataGroup());
    setTimeout(() => {
      getData2("para-system/key/kategori_harga").then((res) => {
        // console.log(res.data.value);
        if (res.data.value === "harga_100%") {
          this.setState({
            kategori_harga: true,
          });
        } else {
          this.setState({
            kategori_harga: false,
          });
        }
  
        this.props.change("kadar", this.state.kategori_harga ? 0 : 100);
      });

    }, 200);
  }
  hitungTotal() {
    this.props.change("sub_total", this.props.sub_total);
  }

  chnageValue(e) {
    // console.log();
    if (e.target.value === "dari_toko") {
      document.getElementById("kode_dari_toko").focus();
      this.props.change("deskripsi_bawa_sediri", "");
      this.props.change("berat_bawa_sendiri", "");
      this.setState({
        dari_toko: false,
        bawa_sendiri: true,
        isChecked: false,
      });
    } else {
      document.getElementById("deskripsi_bawa_sediri").focus();
      this.props.change("kode_dari_toko", "");
      this.props.change("deskripsi_dari_toko", "");
      this.props.change("berat_dari_toko", "");
      this.setState({
        dari_toko: true,
        bawa_sendiri: false,
        isChecked: true,
      });
    }
  }
  getBarcode(e) {
    getData("barang/get/by-kode-barcode/" + e)
      .then((res) => {
        //   console.log(res.data);
        res.data.forEach((element) => {
          this.props.change("deskripsi_dari_toko", element.nama_barang);
          this.props.change("berat_dari_toko", element.berat);
        });
      })
      .catch((err) => {
        // console.log();
        if (err.response.status !== 404) {
          ToastNotification("info", err.response.data);
        }
      });
  }
  hideModal() {
    this.props.dispatch(hideModal());
  }
  setIndex(e) {
    document.getElementById("jenis_barang").focus()
    getData("group/get/by-kode-group/" + e).then((res) => {
      // console.log(res.data);
      this.props.change("harga_pergram",res.data.harga)
    })
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-2">
            <Field
              name="kode_group"
              label="Kategori"
              onChange={(e)=>this.setIndex(e)}
              placeholder="Pilih Kategori"
              options={this.props.DataGroup.filter(
                (list) => list.kode_group !== undefined
              ).map((list) => {
                let data = {
                  value: list.kode_group,
                  name: list.kode_group,
                };
                return data;
              })}
              component={ReanderSelect}
            />
          </div>
          <div className="col-4">
            <Field
              id="jenis_barang"
              name="jenis_barang"
              component={ReanderField}
              type="text"
              label="Nama Barang"
              placeholder="Masukan Nama Barang"
            />
          </div>
          <div className="col-2">
            <Field
              id="betat"
              name="berat"
              component={ReanderField}
              type="text"
              onChange={this.hitungTotal()}
              normalize={Gramasi}
              label="Berat"
              placeholder="Masukan Berat"
            />
          </div>
          <div className={
                this.state.kategori_harga ? "col-lg-2" : "col-lg-2 d-none"
              }>
            <Field
              id="kadar"
              name="kadar"
              component={ReanderField}
              type="text"
              onChange={this.hitungTotal()}
              normalize={NumberOnly}
              label="Kadar"
              placeholder="Masukan Kadar"
            />
          </div>
          <div className={
                this.state.kategori_harga ? "col-lg-2" : "col-lg-4"
              }>
            <Field
              name="harga_pergram"
              component={ReanderField}
              type="text"
              onChange={this.hitungTotal()}
              {...currencyMask}
              label="Harga / Gram"
              placeholder="Masukan Harga / Gram"
            />
          </div>
          <div className="col-6">
            <Field
              id="atribut"
              name="atribut"
              component={ReanderField}
              type="text"
              label="Atribut"
              placeholder="Masukan Atribut"
            />
          </div>
          <div className="col-2">
            <Field
              name="harga_atribut"
              component={ReanderField}
              type="text"
              {...currencyMask}
              label="Harga Atribut"
              onChange={this.hitungTotal()}
              placeholder="Masukan Harga"
            />
          </div>
          <div className="col-2">
            <Field
              name="ongkos"
              component={ReanderField}
              type="text"
              {...currencyMask}
              onChange={this.hitungTotal()}
              label="Ongkos"
              placeholder="Masukan Ongkos"
            />
          </div>
          <div className="col-2">
            <Field
              name="sub_total"
              component={ReanderField}
              type="text"
              {...currencyMask}
              readOnly={true}
              label="Sub Total"
              placeholder="Masukan Sub Total"
            />
          </div>
          <div className="col-12">
            <Field
              name="deskripsi_pesanan"
              component={ReanderField}
              type="text"
              label="Deskripsi"
              placeholder="Masukan Deskripsi"
            />
          </div>
          <div className="col-2">
            <div className="form-group">
              <label>&nbsp;</label>
              <br />
              <label>
                <Field
                  name="options_value"
                  component="input"
                  onClick={(e) => this.chnageValue(e)}
                  type="radio"
                  checked={this.state.isChecked}
                  value="bawa_sendiri"
                />{" "}
                Bawa Sendiri
              </label>
            </div>
          </div>
          <div className="col-7">
            <Field
              id="deskripsi_bawa_sediri"
              name="deskripsi_bawa_sediri"
              component={ReanderField}
              type="text"
              readOnly={this.state.bawa_sendiri}
              label="Deskripsi"
              placeholder="Masukan Deskripsi"
            />
          </div>
          <div className="col-3">
            <Field
              readOnly={this.state.bawa_sendiri}
              name="berat_bawa_sendiri"
              component={ReanderField}
              type="text"
              normalize={Gramasi}
              label="Berat"
              placeholder="Masukan Berat"
            />
          </div>
          <div className="col-2">
            <div className="form-group">
              <label>&nbsp;</label>
              <br />
              <label>
                <Field
                  name="options_value"
                  component="input"
                  type="radio"
                  onClick={(e) => this.chnageValue(e)}
                  value="dari_toko"
                />{" "}
                Dari Toko
              </label>
            </div>
          </div>
          <div className="col-3">
            <Field
              id="kode_dari_toko"
              name="kode_dari_toko"
              onChange={(e) => this.getBarcode(e.target.value)}
              component={ReanderField}
              type="text"
              label="Kode Barcode"
              readOnly={this.state.dari_toko}
              placeholder="Masukan Kode Barcode"
            />
          </div>
          <div className="col-4">
            <Field
              name="deskripsi_dari_toko"
              component={ReanderField}
              type="text"
              label="Deskripsi"
              readOnly={true}
              placeholder="Masukan Deskripsi"
            />
          </div>
          <div className="col-3">
            <Field
              name="berat_dari_toko"
              component={ReanderField}
              type="text"
              normalize={Gramasi}
              label="Berat"
              readOnly={true}
              placeholder="Masukan Berat"
            />
          </div>

          <div className="col-6">
            <button
              className="btn btn-warning"
              type="button"
              onClick={() => this.hideModal()}
            >
              {" "}
              Close{" "}
            </button>
          </div>
          <div className="col-6 text-right">
            <button className="btn btn-primary"> Add </button>
          </div>
        </div>
      </form>
    );
  }
}

ModalTambahBarangPesanan = reduxForm({
  form: "ModalTambahBarangPesanan",
  enableReinitialize: true,
})(ModalTambahBarangPesanan);
export default connect((state) => {
  const selector = formValueSelector("ModalTambahBarangPesanan");
  const { ongkos, kadar, harga_pergram, berat, harga_atribut } = selector(
    state,
    "kadar",
    "berat",
    "harga_pergram",
    "ongkos",
    "harga_atribut"
  );
  let data1 = pembulatan(((parseInt(harga_pergram || 0) * parseInt(kadar || 0)) / parseInt(100)) * parseFloat(berat || 0));
  let hasil = parseInt(data1 || 0) + parseInt(ongkos || 0) + parseInt(harga_atribut || 0);
  return {
    DataGroup: state.datamaster.GetDataGroup,
    sub_total: parseFloat(hasil),
  };
})(ModalTambahBarangPesanan);
